  import validTime from '../../components/base/validTime.vue'
  import selection from '../../components/base/selection.vue'
  import diaLog from './../../components/base/diaLog.vue'
  import bankChooser from './../../components/bankChooser.vue'
  import checkOrder from './../../components/checkOrder.vue'
  import _ from 'lodash'
  export default {
    components: {
      validTime,
      selection,
      mydiaLog:diaLog,
      bankChooser,
      checkOrder
    },
    methods:{
      onChange (attr,val) {
        this[attr]=val
        this.getPrice()
      },
      getPrice () {
        let reqParams = {
          buyType: this.buyType.value,
          area: this.area.value,
        }
        this.$http.get('/Vue-demo/db.json', reqParams)
          .then((res) => {
          this.price=res.data.getPrice.amount

      })
      },
      buy(){
        this.isShow=true
      },
      closeDialog(){
        this.isShow=!this.isShow
      },
      hideErrDialog(){
        this.isShowErrDialog=false
      },
      hideCheckOrder(){
        this.isShowCheckDialog=false
      },
      onChangeBanks(banks){
        this.bankId = banks.id
        console.log(this.bankId)
      },
      configBuy(){
        let reqParams = {
          buyType: this.buyType.value,
          area: this.area.value,
          bankId:this.bankId,
        }
        this.$http.get('/Vue-demo/db.json', reqParams)
          .then((res) => {
          this.orderId=res.data.createOrder.orderId
        this.isShowCheckDialog=true
        this.isShow=false
        console.log(this.orderId)
      },(err) =>{
          this.isShow=false
          this.isShowErrDialog= true
        })
      }
    },
    data () {
      return {
        buyType: {},
        area:{},
        price:222,
        isShow:false,
        bankId:null,
        orderId: null,
