<template>
  <div class="sales-board">
    <div class="sales-board-intro">
      <h2></h2>
      <p>
        </p>
    </div>
    <div class="sales-board-form">
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <buyNumber :max="20" :min="1" @on-change="onChange('buyNum',$event)"></buyNumber>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <productVer :selections="versionList" @on-change="onChange('versions',$event)"></productVer>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          {{price}}
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">&nbsp;</div>
        <div class="sales-board-line-right">
          <div class="button" @click="buy">
            
          </div>
        </div>
      </div>
    </div>
    <div class="sales-board-des">
      <h2></h2>
      <p>
        2020
        
        
        
        </p>
    </div>
    <mydiaLog :is-show="isShow" @on-close="closeDialog">
      <table class="buy-dialog-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>{{ buyNum }}</td>
          <td>
            <span v-for="item in versions">{{ item.label }}</span>
          </td>
          <td>{{price}}</td>
        </tr>
      </table>
      <h3 class="buy-dialog-title"></h3>
      <bankChooser @on-change="onChangeBanks"></bankChooser>
      <div class="button buy-dialog-btn" @click="configBuy"></div>
    </mydiaLog>
    <mydiaLog :is-Show="isShowErrDialog" @on-close="hideErrDialog">
      !
    </mydiaLog>
    <checkOrder :is-show-check-dialog="isShowCheckDialog" @on-close-check-dialog="hideCheckOrder"></checkOrder>
  </div>
</template>

<script>
  import buyNumber from '../../components/base/buyNumber.vue'
  import productVer from '../../components/base/productVerisons.vue'
  import diaLog from './../../components/base/diaLog.vue'
  import bankChooser from './../../components/bankChooser.vue'
  import checkOrder from './../../components/checkOrder.vue'
  import _ from 'lodash'
  export default {
    components: {
      buyNumber,
      productVer,
      mydiaLog: diaLog,
      bankChooser,
      checkOrder
    },
    methods: {
      onChange (attr, val) {
        this[attr] = val
        this.getPrice()
      },
      getPrice () {
        let buyVersionsArray = _.map(this.versions, (item) => {
            return item.value
          })
        let reqParams = {
          buyNumber: this.buyNum,
          version: buyVersionsArray.join(',')
        }
        this.$http.get('/Vue-demo/db.json', reqParams)
          .then((res) => {
          this.price = res.data.getPrice.amount
        console.log(res.data)
      })
      },
      buy(){
        this.isShow = true
      },
      closeDialog(){
        this.isShow = !this.isShow
      },
      hideErrDialog(){
        this.isShowErrDialog = false
      },
      hideCheckOrder(){
        this.isShowCheckDialog = false
      },
      onChangeBanks(banks){
        this.bankId = banks.id
        console.log(this.bankId)
      },
      configBuy(){
        let buyVersionsArray = _.map(this.versions, (item) => {
            return item.value
          })
        let reqParams = {
          buyNumber: this.buyNum,
          bankId: this.bankId,
          version: buyVersionsArray.join(',')
        }
        this.$http.get('/Vue-demo/db.json', reqParams)
          .then((res) => {
          this.orderId = res.data.createOrder.orderId
        this.isShowCheckDialog = true
        this.isShow = false
        console.log(this.orderId)
        },(err)=> {
          this.isShow = false
          this.isShowErrDialog = true
        }
      )
      }
    },
    data () {
      return {
        buyNum: 1,
        price: 333,
        versions: [],
        isShow: false,
        bankId: null,
        orderId: null,
        isShowCheckDialog: false,
        isShowErrDialog: false,
        versionList: [
          {
            label: '',
            value: 0
          },
          {
            label: 'pdf',
            value: 1
          },
          {
            label: '',
            value: 2
          },
          {
            label: '',
            value: 3
          }
        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }

  .buy-dialog-btn {
    margin-top: 20px;
  }

  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }

  .buy-dialog-table td,
  .buy-dialog-table th {
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }

  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }

  .buy-dialog-table span {
    padding: 0px 5px 0px 5px;
  }
</style>



